@use "../utils" as *;

//Edytować poniżej
@mixin pseudoelement ($width, $height) {
    content: "";
    display: inline-block;
    width: $width;
    height: $height;
    background-size: contain;
    background-color: transparent;
    background-repeat: no-repeat;
}

.howWeDo {
    @include container;
    @include flexCenter(column);
    padding: 205px 20px 50px 20px;


    &__heading {
        @include sectionHeader(20px);
        line-height: 1.3px;
        letter-spacing: .03em;
        color: $primary-text;
        margin-bottom: 30px;
    }

}

.grid {
    @include flexCenter(column);
    gap: 15px;

    &__item {
        text-align: center;
        box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);
        width: 100%;
        height: 280px;
        padding: 30px 10px 10px;
    }

    &__item--highlighted{
        @extend .grid__item;
       background-color: $theme;
    }

    &__icon{
        fill: $theme;
        margin-bottom: 20px;
    }

    &__icon--highlighted{
        @extend .grid__icon;
        fill: $contrast-text;
       
    }

    &__title {
        font-weight: 700;
        font-size: 14px;
        line-height: 1.428;
        letter-spacing: .08 em;
        margin-bottom: 32px;
        
    
    }

  


    &__title--highlighted{
        @extend .grid__title;
       color: $contrast-text;
       
    }

    &__text {
        color: $secondary-text;
        font-size: 12px;
        line-height: 1.666;
        letter-spacing: .08 em;
        position: relative;
    }

&__text::before{
        @include pseudoelement(16.52px,13.54px);
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='3' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 .4h20v1.94H0V.4Z' fill='%23FF6B09'/%3E%3C/svg%3E");
        position: absolute;
        left: 50%;
        top: -16px;
        transform: translateX(-50%);
      }

    &__text--highlighted{
        @extend .grid__text;
       color: $contrast-text;
       
    }

    &__text--highlighted::before{
        @include pseudoelement(16.52px,13.54px);
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='3' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 .4h20v1.94H0V.4Z' fill='%23fff'/%3E%3C/svg%3E");
        position: absolute;
        left: 50%;
        top: -16px;
        transform: translateX(-50%);
    }
    


}